export const BASE_URL = 'http://cheltsovsmesto.nomoredomains.club/sign-in';

const getResponseData = (response, status) => {
  console.log(response)
  try {
    console.log(response.status)
    if (response.status === status){
      return response.json();
    }
  } catch(e){
    console.log(e)
    return (e)
  }
} 

export const registration = (password, email) => {
  return fetch('https://cheltsovsmesto.nomoredomains.club/signup', {
    method: 'POST',
    headers: {
        "Content-Type": "application/json"
    },
    body: JSON.stringify({password: password,
    email: email})
  })
  .then(response => getResponseData(response, 201))
};

export const login = (password, email) => { 
  return fetch('https://cheltsovsmesto.nomoredomains.club/signin', {
    method: 'POST',
    headers: {
        "Content-Type": "application/json"
    },
    body: JSON.stringify({password: password,
    email: email})
  })
  .then(response => getResponseData(response, 200))
};

export const getUserInfo = (jwt) => { 
  return fetch('https://cheltsovsmesto.nomoredomains.club/users/me', {
    method: 'GET',
    headers: {
      "Content-Type": "application/json",
      "authorization" : `Bearer ${jwt}`
    },
  })
  .then((response) => {
    console.log(response)
    getResponseData(response, 200)
  })
};