import { Promise } from "core-js";

export class Api {
    constructor(options) {
        this._options = options;
        this._token = options.token;
        this._address = options.address;
        this._owner = options.owner;
    }

    _checkResult() {
    return  (res) => {
        if (res.ok) {
          return res.json();
        }
        return Promise.reject(res.status);
      }
    }

    getUserInfoFromServer() {
        return fetch(`${this._address}/users/me`, {
            method: 'GET',
            headers: {
                "Accept": 'application/json',
                "authorization": 'Bearer ' + localStorage.getItem('token'),
                "Access-Control-Allow-Origin": 'https://cheltsovsmesto.nomoredomains.club',
                'Content-Type': 'application/json',
                credentials: 'include'
              }
        }).then(this._checkResult()).then((res) => {
          return res
        })
    }

    pushNewAvatar(link) {
        return fetch(`${this._address}/users/${this._owner}/avatar`, {
            method: 'PATCH',
            headers: {
              "authorization": 'Bearer ' + localStorage.getItem('token'),
              "Accept": 'application/json',
              "Access-Control-Allow-Origin": 'https://cheltsovsmesto.nomoredomains.club',
              'Content-Type': 'application/json',
              credentials: 'include'
            },
            body: JSON.stringify({
              avatar: link
            })
          }).then(this._checkResult()).then((res) => {
            return res
          })
    }

    getAllNeededDataForCards() {
      return Promise.all([this.getUserInfoFromServer(), this.getCards()])
    }

    pushNewUserInfo(UserName, UserAbout) {
      return fetch(`${this._address}/users/me`, {
            method: 'PATCH',
            headers: {
              "authorization": 'Bearer ' + localStorage.getItem('token'),
                "Accept": 'application/json',
                "Access-Control-Allow-Origin": 'https://cheltsovsmesto.nomoredomains.club',
                'Content-Type': 'application/json',
                credentials: 'include'
            },
            body: JSON.stringify({
                name: UserName,
                about: UserAbout
            })
            }).then(this._checkResult()).then((res) => {
            return res
            })
    }

    deleteLike(cardId) {
      return fetch(`${this._address}/cards/${cardId}/likes`, {
            method: 'DELETE',
            headers: {
              "authorization": 'Bearer ' + localStorage.getItem('token'),
              "Accept": 'application/json',
              "Access-Control-Allow-Origin": 'https://cheltsovsmesto.nomoredomains.club',
              'Content-Type': 'application/json',
              credentials: 'include'
            }
          }).then(this._checkResult()).then((res) => {
            return res
          })
    }

    addLike(cardId) {
      return fetch(`${this._address}/cards/${cardId}/likes`, {
            method: 'PUT',
            headers: {
              "authorization": 'Bearer ' + localStorage.getItem('token'),
              "Accept": 'application/json',
              "Access-Control-Allow-Origin": 'https://cheltsovsmesto.nomoredomains.club',
              'Content-Type': 'application/json',
              credentials: 'include'
            }
          }).then(this._checkResult()).then((res) => {
            return res
          })
    }

    removeCard(cardId) {
      console.log('kkk')
      return fetch(`${this._address}/cards/${cardId}`, {
            method: 'DELETE',
            headers: {
              "authorization": 'Bearer ' + localStorage.getItem('token'),
              "Accept": 'application/json',
              "Access-Control-Allow-Origin": 'https://cheltsovsmesto.nomoredomains.club',
              'Content-Type': 'application/json',
              credentials: 'include'
            }
          }).then(this._checkResult()).then((res) => {
            return res
          })
    }
    
    getCards() {
        return fetch(`${this._address}/cards`, {
            method: 'GET',
            headers: {
            "Accept": 'application/json',
            "Access-Control-Allow-Origin": 'https://cheltsovsmesto.nomoredomains.club',
            "authorization": 'Bearer ' + localStorage.getItem('token'),
            credentials: 'include'
            }
        }).then(this._checkResult()).then((res) => {
          return res
        })
    }

    pushCard(name, link) {
        return fetch(`${this._address}/cards`, {
            method: 'POST',
            headers: {
              "authorization": 'Bearer ' + localStorage.getItem('token'),
              "Access-Control-Allow-Origin": 'https://cheltsovsmesto.nomoredomains.club',
              'Content-Type': 'application/json',
              credentials: 'include'
            },
            body: JSON.stringify({
              name: name,
              link: link
            })
          }).then(this._checkResult()).then((res) => {
            return res
          })
    }

}

const setings = {
  address: 'https://cheltsovsmesto.nomoredomains.club',
  owner: 'me',
}


const myApi = new Api(setings)
export default myApi
